.homeBackground{
    background-image: url('../../assets/images/background/home.jpg');
    z-index: -1;
    position: absolute;
    height: 87vh;
    width: 100%;
    background-position: right;
    background-size: contain;
    background-repeat: no-repeat;
    background-color: var(--white);
}

.textContainer{
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    flex-direction: column;
    position: relative;
    z-index: 2;
}

.textContainer h2{
    width: 70%;
    font-size: 40px;
    text-align: center; 
    font-weight: var(--fontWeightLight);
    color: var(--black);
}

.textContainer span{
    background: linear-gradient(135deg, var(--red), var(--black));
    -webkit-background-clip: text;
    color: transparent;
    font-weight: var(--fontWeightBold);
}

.textContainer h2 b{
    font-weight: var(--fontWeightBold);
    color: var(--black);
}

.textContainer h2 u{
    color: var(--red);
}

.textContainer button{
    border: none;
    padding: 12px 40px;
    font-size: 16px;
    border-radius: var(--borderRadius);
    background: linear-gradient(135deg, var(--red), var(--black));
    color: var(--white);
    font-weight: var(--fontWeightMedium);
    position: relative;
    z-index: 5;
    cursor: pointer;
    transition: var(--animationAll);
}

.textContainer button:hover{
    background: var(--white);
    color: var(--black);
    border: var(--borderSize) var(--black);
    padding: 13px 50px;
}

.textContainer hr {
    background-color: transparent;
    height: 2px;
    border: none; 
    border-top: 1px dashed var(--black); 
    position: absolute;
    transform: rotate(90deg);
    bottom: 20%;
    width: 200px;
}

.DadosContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 7vh;
    padding: 0 5%;
}

.DadosLeft p{
    color: var(--black);
    font-weight: var(--fontWeightLight);
    font-size: 14px;
}

.DadosLeft p b{
    font-weight: var(--fontWeightMedium);
}

.DadosRight p{
    color: var(--black);
    font-weight: var(--fontWeightLight);
    font-size: 14px;
}

.DadosRight a{
    color: var(--black);
    font-size: 14px;
    font-weight: var(--fontWeightMedium);
}

/* Responsive */

@media (max-width: 600px){
    .homeBackground{
        background-image: url(../../assets/images/background/homeMobile.png);
        height: 92dvh;
    }
    .textContainer{
        height: 82dvh;
        padding: 0 5%;
        gap: 30px;
    }
    .textContainer button{

    }
    .textContainer hr{
        width: 250px;
        bottom: 15%;
    }
    .textContainer h2{
        font-size: 28px;
        width: 100%;
    }
    .DadosContainer{
        display: none;
    }  
}